.calendar-header{display:grid;grid-gap:2px;background-color:#3F3D40;grid-template-columns:26% repeat(12, 1fr);grid-template-rows:50px 30px}.calendar .header-cell{padding-top:10px;background-color:#7D7A80;color:#fff;font-size:16px;font-weight:bold}.header-cell.cell-label{order:-1;grid-column-start:1;grid-column-end:2;grid-row-start:1;grid-row-end:2;text-align:left;padding-left:20px;color:#F1F1F1;font-size:16px;font-weight:bold}.header-cell.cell-1{order:1;grid-column-start:2;grid-column-end:5;grid-row-start:1;grid-row-end:2;text-align:center}.header-cell.cell-2{order:2;grid-column-start:5;grid-column-end:8;grid-row-start:1;grid-row-end:2;text-align:center}.header-cell.cell-3{order:3;grid-column-start:8;grid-column-end:11;grid-row-start:1;grid-row-end:2;text-align:center}.header-cell.cell-4{order:4;grid-column-start:11;grid-column-end:14;grid-row-start:1;grid-row-end:2;text-align:center}.sub-header-cell{background-color:#D8D8D8;text-align:center}.sub-header-cell:hover{cursor:pointer;font-weight:bold}.sub-header-cell.active{background-color:#3F3D40;color:#fff;font-weight:bold}.sub-header-cell.label-cell{grid-column-start:1;grid-column-end:2;grid-row-start:2;grid-row-end:3}

.calendar-cell{text-align:center;padding-top:15px}.calendar-cell .icon{text-align:center;width:80%;margin:0px auto}.calendar-cell .status{display:none}.calendar-cell:hover{cursor:pointer}.calendar-cell:hover .icon{display:none}.calendar-cell:hover .status{display:block}.calendar-cell.active{font-weight:bold;color:#3F3D40;border-left:outset 2px #3F3D40;border-right:outset 2px #3F3D40}.calendar-cell.price{padding-top:8px;font-size:14px}.calendar-cell.price.none{padding-top:15px;font-size:16px}.calendar-cell.price span{display:block;font-size:11px}.calendar-cell.early{background-color:#F3EB90;color:#776f0d}.calendar-cell.peak{background-color:#B1D095;color:#648d3f}.calendar-cell.late{background-color:#F3EB90;color:#776f0d}.calendar-cell.none,.calendar-cell.off{background-color:#D8D8D8}

.calendar-row{display:grid;grid-gap:1px;background-color:#7D7A80;grid-template-columns:26% repeat(12, 1fr);grid-template-rows:50px}.calendar-row:hover{border-top:inset 2px #3F3D40;border-bottom:inset 2px #3F3D40}.calendar-row:hover .cell-label{font-weight:bold}.calendar-cell.cell-label{order:-1;text-align:left;padding-left:20px;color:#3F3D40;font-size:16px;background-color:#fff}.calendar-cell.cell-1{grid-row-start:1;grid-row-end:2;grid-column-start:2;grid-column-end:3}.calendar-cell.cell-2{grid-row-start:1;grid-row-end:2;grid-column-start:3;grid-column-end:4}.calendar-cell.cell-3{grid-row-start:1;grid-row-end:2;grid-column-start:4;grid-column-end:5}.calendar-cell.cell-4{grid-row-start:1;grid-row-end:2;grid-column-start:5;grid-column-end:6}.calendar-cell.cell-5{grid-row-start:1;grid-row-end:2;grid-column-start:6;grid-column-end:7}.calendar-cell.cell-6{grid-row-start:1;grid-row-end:2;grid-column-start:7;grid-column-end:8}.calendar-cell.cell-7{grid-row-start:1;grid-row-end:2;grid-column-start:8;grid-column-end:9}.calendar-cell.cell-8{grid-row-start:1;grid-row-end:2;grid-column-start:9;grid-column-end:10}.calendar-cell.cell-9{grid-row-start:1;grid-row-end:2;grid-column-start:10;grid-column-end:11}.calendar-cell.cell-10{grid-row-start:1;grid-row-end:2;grid-column-start:11;grid-column-end:12}.calendar-cell.cell-11{grid-row-start:1;grid-row-end:2;grid-column-start:12;grid-column-end:13}.calendar-cell.cell-12{grid-row-start:1;grid-row-end:2;grid-column-start:13;grid-column-end:14}

.calendar{width:95%;margin:2% auto 10px auto;overflow:hidden;background-color:#ededed;border:solid 2px #3F3D40}.calendar-body{max-height:80vh;overflow:scroll;border-top:solid 2px #3F3D40}@media (max-width: 1024px){.calendar-body{max-height:60vh}}

.search-field{margin:20px 0px}.search-field input{color:#F1F1F1;font-size:14px;width:80%;padding:6px 6px 6px 2px;background-color:transparent;border-top:none;border-left:none;border-right:none;border-bottom:solid 2px #F1F1F1}.search-field input::-webkit-input-placeholder{color:#F1F1F1}.search-field input:-ms-input-placeholder{color:#F1F1F1}.search-field input::-ms-input-placeholder{color:#F1F1F1}.search-field input::placeholder{color:#F1F1F1}.search-field input:focus{outline-width:0}

button:focus{outline:none}button.primary{font-size:14px;padding:2px 6px 2px 4px;color:#F1F1F1;border:none;background-color:transparent;position:relative}button.primary.active{font-size:14px;padding:4px 8px 4px 28px;color:#3F3D40;background-color:#D8D8D8;border-radius:8px;border:solid 1px #D8D8D8}button.primary:hover{font-weight:bold;cursor:pointer}button .icon{position:absolute;left:6px;top:6px}

.season-picker{margin-top:30px}.season-picker button{display:block;margin:10px 0px}

.pricing-toggle{margin:40px 0px}.pricing-toggle a.toggle{background:#D8D8D8;cursor:pointer;border:2px solid black;border-right-width:30px;padding:0 5px;border-radius:5px;text-decoration:none;-webkit-transition:all .5s ease;transition:all .5s ease}.pricing-toggle a.toggle.off{background:#BDBDBD;border-right-width:2px;border-left-width:30px}

.sidebar{padding:40px 22px;background-color:#3F3D40;color:#F1F1F1}.sidebar h2{margin:0px}.sidebar h4{margin:10px 0px}.sidebar span{font-size:13px}.sidebar hr{border:solid 1px #F1F1F1}.sidebar .menu-item{margin:10px 0px}.sidebar .menu-item a{font-size:13px;color:#F1F1F1}

body{margin:0px;padding:0px;font-family:'Source Sans Pro', sans-serif}a{color:#3F3D40;font-weight:bold;text-decoration:none}.app{display:grid;overflow:hidden;height:100vh;grid-template-columns:280px 1fr;grid-template-rows:50px 1fr;background-color:#fff}.app .sidebar{grid-column-start:1;grid-column-end:2;grid-row-start:1;grid-row-end:3}@media (max-width: 1024px){.app .sidebar{grid-column-start:1;grid-column-end:3;grid-row-start:1;grid-row-end:2}}.app .main{grid-column-start:2;grid-column-end:3;align-self:stretch;grid-row-start:1;grid-row-end:3;background-color:#F1F1F1}@media (max-width: 1024px){.app .main{grid-column-start:1;grid-column-end:3;grid-row-start:2;grid-row-end:3}}.app .acknowledgement{width:95%;margin:0 auto;text-align:right;font-size:12px;font-family:monospace}

